import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {
  constructor() {}

  ngOnInit() {}

  get nbYearOfExperience(): number {
    const tagIpDebut = new Date('04/01/2014');
    const tagIpEnd = new Date('04/01/2015');
    const nlDebut = new Date('09/01/2015');
    const today = new Date();
    return tagIpEnd.getFullYear() - tagIpDebut.getFullYear() + (today.getFullYear() - nlDebut.getFullYear());
  }
}
